import React, { useEffect, useState } from "react";
import styled, { keyframes } from "styled-components";

const fadeInBottom = keyframes`
    to {
        opacity: 100%;
        transform: translateY(0px);
    }
`;

const StyledDescription = styled.p`
	opacity: 0%;
	transform: translateY(10px);
	height: 2rem;
	font-size: 1.5rem;
	font-family: ${(props) => props.fuente};
	// font-weight: 300;
	text-align: center;
	letter-spacing: 0.5em;
	animation: ${fadeInBottom} 0.5s 2.1s ease forwards;

	@media (max-width: 768px) {
		font-size: 1rem;
	}
`;

function Descriptions(props) {
	const [actual, setActual] = useState(0);

	const fuentes = [
		"'Roboto', sans-serif",
		"'Benne', 'Big Shoulders Stencil Display', cursive",
		"'Jomhuria', cursive",
		"'Yellowtail', cursive",
		"font-family: 'Bebas Neue', cursive",
	];

	const cambiarFuente = () => {
		setActual((pasado) => (pasado + 1) % fuentes.length);
	};

	useEffect(() => {
		setTimeout(() => setInterval(cambiarFuente, 1000), 2600);
	}, []);

	return (
		<StyledDescription fuente={fuentes[actual]}>
			desarrollador web
		</StyledDescription>
	);
}

export default Descriptions;
